export default {
	data() {
		return {
			showVideo: true,
			videoSrc:"",
			videoContext: null,
			ratingsList: [],
			labelIndex: 0, // 当前评论的下标
			labelList: [{
					name: '全部',
					number: 25,
					type: 'all'
				},
				{
					name: '好评',
					number: 23,
					type: 'good'
				},
				{
					name: '中评',
					number: 1,
					type: 'secondary'
				},
				{
					name: '差评',
					number: 1,
					type: 'poor'
				},
				{
					name: '有图',
					number: 12,
					type: 'img'
				},
				{
					name: '视频',
					number: 2,
					type: 'video'
				},
				{
					name: '追加',
					number: 2,
					type: 'append'
				}

			]
		}
	},
	onLoad() {

		this.showVideo = false

		try {
			const comments = uni.getStorageSync('comments');
			if (comments) {
				this.ratingsList = comments;
				// console.log(this.ratingsList);
			}
		} catch (e) {
			//TODO handle the exception
			console.log(e);
		}
	},
	methods: {
		loadRatings(index) {
			this.labelIndex = index;
		},
		showBigImg(image,images){
			uni.previewImage({
				current:image,
				urls: images
			})
		},
		playVideo(path){
			
			this.videoSrc = path;
			
			// 全屏播放
			this.$nextTick(() => {
				this.videoContext.requestFullScreen({
					direction:0
				})
			})
			

			this.showVideo = true

		},
		videoControl(e){
			
			if(e.detail.fullScreen){ // 全屏
				// 自动播放
				this.videoContext.play();
			}else {
				// 停止播放
				this.videoContext.stop()
				this.videoPause();
			}
		},
		videoPause(){
			this.videoSrc = '';

			this.showVideo = false

		}
	},
	onReady() {
	 this.videoContext = uni.createVideoContext('myVideo');
	}
}
